import React, { useState } from "react";
import { useCart } from 'react-use-cart';
import Toast from 'react-bootstrap/Toast';
import { Row, Col, Button } from 'react-bootstrap';


export default function Proditem(props) {
    const [show, setShow] = useState(false);

    const { addItem } = useCart();

    const mystyle = {
        width: "18rem",
        height: "18rem",
    };

    let { title, price, status, image, rating, item } = props;

    return (
        <div className="my-3 mx-4">
            <div className="card">
                <div style={{
                    display: 'flex',
                    justifyContent: 'flex-end',
                    position: 'absolute',
                    right: '0'
                }} >
                </div>
                <img src={!image ? "https://bitsofco.de/content/images/2018/12/broken-1.png" : image} className="card-img-top" style={mystyle} alt="404 Error" />
                <div className="card-body">
                    <p className="card-title">{title}</p>
                    <p className="card-text"><small></small><strong>{price}</strong></p>
                    <p className="card-text">Status: {status}</p>
                </div>
                {/* <button onClick={() => addItem(doc.item)}>Add to basket</button> */}
                <button className="btn btn-success" onClick={() => { addItem(item); setShow(true) }}>Add to cart</button>
                <Row>
                    <Col xs={6}>
                        <Toast onClose={() => setShow(false)} show={show} delay={3500} autohide>
                            <Toast.Header style={{backgroundColor: "black"}}>
                                <img
                                    src="holder.js/20x20?text=%20"
                                    className="rounded me-2"
                                    alt=""
                                />
                                <strong className="me-auto text-warning">Item added to cart!</strong>
                            </Toast.Header>
                            {/* <Toast.Body className="text-success" style={{backgroundColor: "black"}}> Check your cart</Toast.Body> */}
                        </Toast>
                    </Col>
                </Row>
            </div>
        </div>
    )
}
