import React, { useState, useEffect } from 'react';
import { useCart } from 'react-use-cart';
import TextField from '@material-ui/core/TextField';

export default function Invoice() {
    const {
        items,
        cartTotal,
        discount = 0,
    } = useCart();

    var [date, setDate] = useState(new Date());

    useEffect(() => {
        var timer = setInterval(() => setDate(new Date()), 1000)
        return function cleanup() {
            clearInterval(timer)
        }

    });

    return (
        <div className="container-fluid">
            <div className="col">
                <div className="card h-100">
                    <div className="card-body">
                        <h5 className="card-title text-center">Invoice</h5>
                        <hr />
                        <div className="p-2 w-100">
                            <p className="text-start my-0"><b><i>PSP BASKET</i></b></p>
                            <p className="text-start my-0"><i>Wani.</i></p>
                            <p className="text-start my-0"><i>9922015121</i></p>
                            <p className="text-start my-0"><i>prakalpsp16@gmail.com</i></p>
                        </div>
                        <div className="d-flex">
                            <div className="p-2 w-100">
                                <p className="text-start"><b>Bill to:</b></p>
                                <p className="text-start"><b>Buyers Name.</b>Prakalp Pande</p>
                                <p className="text-start"><b>Buyers Address.</b></p>
                                <p className="text-start"><b>Buyers Phone No.</b></p>
                                <p className="text-start"><b>Buyers Email.</b></p>
                            </div>
                            <div className="p-2 flex-shrink-1">
                                <p className="text-end"><b>Invoice Number</b> xxxx.</p>
                                <p className="text-end"><b>Date :</b> {date.toLocaleDateString()}</p>
                                <p className="text-end"><b>Time :</b> {date.toLocaleTimeString()}</p>
                                <p className="text-end mx-3"><b>Payment due-</b>
                                    <TextField
                                        id="date"
                                        // label="Choose your paymentdue"
                                        type="date"
                                        // defaultValue="2022-07-16"
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    /></p>
                            </div>
                        </div>

                        <div className="container">
                            <table className="table">
                                <thead>
                                    <tr className="table-info">
                                        <th scope="col">#</th>
                                        <th scope="col">Total items</th>
                                        <th scope="col">Quantity</th>
                                        <th scope="col">Price</th>
                                        <th scope="col">Amount</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {items.map((item, index) => {
                                        return (
                                            <tr key={index}>
                                                <th scope="row">{index + 1}</th>
                                                <td>{item.title}</td>
                                                <td>{item.quantity}</td>
                                                <td>{item.price}</td>
                                                <td>{item.quantity * item.price}</td>
                                            </tr>
                                        )
                                    })}
                                    <tr>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                    </tr>
                                    <tr>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                        <th scope="col">SubTotal</th>
                                        <th scope="col"></th>
                                        <td scope="col">{cartTotal}</td>
                                    </tr>
                                    <tr>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                        <th scope="col">Discount</th>
                                        <td scope="col">2%</td>
                                        {/* <th scope="col">{discount}</th> */}
                                        <td scope="col">{cartTotal >= 5000 ? cartTotal * 0.02 : discount}</td>
                                    </tr>
                                    <tr>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                        <th className="table-info" scope="col">Amount</th>
                                        <th className="table-info" scope="col"></th>
                                        <th className="table-info" scope="col"><i>{cartTotal >= 5000 ? cartTotal - (cartTotal * 0.05) : cartTotal}</i></th>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div className="card-footer">
                        <small className="text-muted"><i>THANK YOU FOR YOUR PARTICIPATION. HAVE A GRATEFUL DAY!</i><br /><b>Wish you Happy Diwali...</b></small>
                    </div>
                </div>
            </div>
        </div>
    )
}
