import React from 'react';
import { Link } from 'react-router-dom';

export default function Ecommerce() {
    return (
        <div className="container my-3">
        <h3 className='text-secondary my-3'>A place where we meet your expectations...</h3>
            <div className="card mb-3">
                <div className="row g-0">
                    <div className="col-md-4">
                        <img src="https://m.media-amazon.com/images/G/31/cart/empty/kettle-desaturated._CB424694257_.svg" className="img-fluid rounded-start" alt="..." />
                    </div>
                    <div className="col-md-8">
                        <div className="card-body">
                            <h4 className="card-title mt-4">Your PSP Basket is Empty</h4>
                            <Link to={"/"} className="card-text text-decoration-none text-success" ><p>Shop today's deal</p>
                                <button type="button" className="btn btn-outline-dark mx-2">Tap here to view product's</button></Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
