import React, { useEffect, useState } from "react";
import { useCart } from 'react-use-cart';
import { doc } from "firebase/firestore";
import ProductDataService from "../services/product.services";
import { Link } from "react-router-dom";
import Toast from 'react-bootstrap/Toast';
import { Row, Col, Button } from 'react-bootstrap';
import Popover from 'react-bootstrap/Popover';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Invoice from "./Invoice";

const ProductCart = ({ getProductId }, props) => {

    const [show, setShow] = useState(false);

    const popover = (
        <Popover id="popover-basic">
            <Popover.Header as="h3">Sorry!</Popover.Header>
            <Popover.Body>
                This feature will available soon!
            </Popover.Body>
        </Popover>
    );

    const [products, setProduct] = useState([]);
    useEffect(() => {
        getProducts();
    }, []);

    const getProducts = async () => {
        const data = await ProductDataService.getAllProducts();
        console.log(data.docs);
        setProduct(data.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
    };

    const {
        isEmpty,
        totalUniqueItems,
        items,
        totalItems,
        cartTotal,
        updateItemQuantity,
        removeItem,
        // emptyCart,
    } = useCart();
    if (isEmpty) return <div className="container my-4 shadow p-3 mb-5 bg-body rounded">
        <div className="card mb-3">
            <div className="row g-0">
                <div className="col-md-4">
                    <img src="https://m.media-amazon.com/images/G/31/cart/empty/kettle-desaturated._CB424694257_.svg" className="img-fluid rounded-start" alt="..." />
                </div>
                <div className="col-md-8">
                    <div className="card-body">
                        <h4 className="card-title mt-4">Your PSP Basket is Empty</h4>
                        <Link to={"/"} className="card-text text-decoration-none" ><p>Shop today's deal</p></Link>
                        <button type="button" className="btn btn-warning mx-2">Sign in to your account</button>
                        <button type="button" className="btn btn-light">Sign up now</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    return (
        <div className="row row-cols-1 row-cols-md-2 g-4 mx-2">
            <div className="py-4 container-fluid g-col-6">
                <h5>Cart ({totalUniqueItems}) total Items: ({totalItems}) total cost: ({cartTotal}) </h5>
                {items.map((item, index) => {
                    return (
                        <div className="card mb-3 mx-3" style={{ maxWidth: "540px" }} key={index}>
                            <div className="row g-0">
                                <div className="col-md-4">
                                    <img src={item.image} style={{ height: '18 rem', maxWidth: "100%" }} alt="" />
                                </div>
                                <div className="col">
                                    <div className="card-body">
                                        <h5 className="card-title">{item.title}</h5>
                                        <p className="card-text"> {item.price}</p>
                                        <p className="card-text">Quantity ({item.quantity}) </p>
                                        <p className="card-text">Sub Total:-  {item.quantity * item.price} </p>
                                        <p className="fw-lighter">Eligible for FREE Shipping.</p>
                                        <div className="form-check">
                                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault" />
                                            <label className="form-check-label" htmlFor="flexCheckDefault">
                                                This will be a gift
                                            </label>
                                        </div>
                                    </div>
                                    <button
                                        className="btn btn-outline-dark ms-2"
                                        onClick={() => updateItemQuantity(item.id, item.quantity - 1)}
                                    >-</button>
                                    <button
                                        className="btn btn-outline-dark ms-2"
                                        onClick={() => updateItemQuantity(item.id, item.quantity + 1)}
                                    >+</button>
                                    <button className="btn btn-danger ms-2 my-2" onClick={() => removeItem(item.id)}>Remove Item</button>
                                    {/* <button type="button" className="btn btn-success mx-2" id="liveToastBtn">Buy now</button> */}

                                    <OverlayTrigger trigger="click" placement="bottom" overlay={popover} autohide>
                                        <Button className="mx-2" variant="outline-success">Buy now</Button>
                                    </OverlayTrigger>

                                    {/* <Row>
                                        <Col xs={6}>
                                            <Toast className="container position-fixed bottom-0 end-0 p-3 my-3" onClose={() => setShow(false)} show={show} delay={3000} autohide>
                                                <Toast.Header>
                                                    <img
                                                        src="holder.js/20x20?text=%20"
                                                        className="rounded me-2"
                                                        alt=""
                                                    />
                                                    <strong className="me-auto">PSP</strong>
                                                </Toast.Header>
                                                <Toast.Body>Woohoo, you're reading this text in a Toast!</Toast.Body>
                                            </Toast>
                                        </Col>
                                        <Col xs={6}>
                                            <Button onClick={() => setShow(true)}>Show Toast</Button>
                                        </Col>
                                    </Row> */}

                                </div>
                            </div>
                        </div>
                    )
                })}

            </div>
            <div className="container g-col-6">
                <Invoice/>
            </div>
        </div>
    )
}

export default ProductCart;

